import axios from 'axios'
import TitleBar from 'shared/components/TitleBar'
import { useEffect, useState } from 'react'

import QrReader from 'modern-react-qr-reader'
import { useSelector } from 'react-redux'
import { useNavigate } from 'react-router-dom'
import { selectUser } from 'store/userSlice'

import { authHeader } from 'utils'
import { BikeUserInfo, UploadCard } from './components'
import styles from './styles.module.css'

export function BicyclesPage() {
  const navigate = useNavigate()
  const user = useSelector(selectUser)
  const [bicycleList, setBicycleList] = useState([])
  const [bikeUser, setBikeUser] = useState(null)
  const [scanner, setScanner] = useState(null)
  const [scanned, setScanned] = useState(null)

  useEffect(() => {
    axios
      .get('/api/bike-user', authHeader())
      .then(res => {
        if (res.data.data.length > 0) {
          let bikeUserInfo = res.data.data[0]
          bikeUserInfo.inTime = new Date(bikeUserInfo.inTime)

          if (bikeUserInfo.outTime == null) {
            bikeUserInfo.state = 0
          } else {
            bikeUserInfo.state = 1
            bikeUserInfo.outTime = new Date(bikeUserInfo.outTime)

            //calc total time used
            const totalTime = bikeUserInfo.outTime.getTime() - bikeUserInfo.inTime.getTime()
            const totalDates = Math.floor(totalTime / 86400000)
            const totalHours = Math.floor(totalTime / 3600000)
            const leftHours = totalHours - totalDates * 24
            const leftMinutes = Math.floor(totalTime / 1000 / 60) - 60 * totalHours
            bikeUserInfo.totalHours = leftHours
            bikeUserInfo.totalMinutes = leftMinutes
            bikeUserInfo.totalDates = totalDates

            //calc total price
            if (leftMinutes <= 15) {
              bikeUserInfo.totalPrice = bikeUserInfo.totalHours * bikeUserInfo.bike.hourPrice
            } else {
              bikeUserInfo.totalPrice = (bikeUserInfo.totalHours + 1) * bikeUserInfo.bike.hourPrice
            }

            if (bikeUserInfo.totalPrice > bikeUserInfo.bike.dayPrice) {
              const dayNum = bikeUserInfo.outTime.getDate() - bikeUserInfo.inTime.getDate() + 1

              bikeUserInfo.totalPrice = bikeUserInfo.bike.dayPrice * dayNum
            }

            console.log(bikeUserInfo.totalPrice)
          }
          setBikeUser(bikeUserInfo)
        }
      })
      .catch(err => {
        console.log(err)
      })
  }, [])

  useEffect(() => {
    axios
      .get('/api/bike')
      .then(res => {
        setBicycleList(res.data.data)
      })
      .catch(err => alert(err.toString()))
  }, [])

  const handleBicycleButtonClick = child => {
    if (!child.isAvailable) {
      return
    }

    navigate(`/bicycle?id=${child._id}`)
  }

  const handleReturnBikeButton = () => {
    setScanner(true)
  }

  const handleError = error => {
    console.log(error)
    alert(
      'ng dng khng c quyn truy cp camera. Vui lng cp quyn camera trn trnh duyt ca bn. Hoc lin h: 0797324886  c h tr nhanh nht.'
    )
    navigate(0)
  }

  const onNewScanResult = data => {
    if (data) {
      setScanner(false)
      setScanned(true)

      axios
        .patch('/api/bike-user', { bikeToken: data }, authHeader())
        .then(res => {
          if (res.status === 200) {
            console.log(res.data.data)
            alert('Bn  tr xe thnh cng!')
            navigate(0)
          }
        })
        .catch(err => {
          alert(
            ' c li xy ra. Vui lng chp mn hnh li gi v zalo: 0797324886  c h tr x l nhanh nht! Li: ' +
              err.toString()
          )
        })
    }
  }

  return (
    <div className={styles.container}>
      {/* <TitleBar title="Xe p cng cng" /> */}
      <img src="/bicyclebanner.jpg" alt="banner" width={'100%'} />
      <div
        style={{
          display: 'flex',
          justifyContent: 'center',
          flexDirection: 'column',
          alignItems: 'center'
        }}
      >
        <p
          style={{
            fontWeight: 'bold',
            color: 'red',
            fontSize: '0.8rem',
            textAlign: 'center',
            margin: '0.5rem 0'
          }}
        >
          Quan trng cn lm trc khi n im mn
        </p>
        <p
          style={{
            color: 'var(--primary)',
            fontSize: '0.8rem',
            textAlign: '',
            width: '90%',
            margin: '0'
          }}
        >
          Bc 1:
          <br />
          Cp nht giy t ty thn
          <br /> Bc 2:
          <br />
          Kim tra hot ng Camera bng cch:
          <br />
          - Chn xe p v bm Qut QR
          <br />
          - Cp quyn camera cho ng dng
          <br />
          * Hng dn khc phc li:
          <br />- Khng cp nht c giy t ty thn (Li 401): vui lng ng xut ti khon v ng nhp li  khc
          phc.
          <br />- Khng m c camera: Cp quyn camera cho ng dng trn trnh duyt ang s dng hoc ti trnh duyt
          Firefox v t CH Play/App Store  truy cp vo website.
          <br />- Lin h h tr k thut ti Zalo:{' '}
          <a href="https://zalo.me/0797324886" target="_blank" rel="noopener noreferrer">
            0797324886
          </a>
        </p>
        <div className={styles.mapButtonContainer}>
          <a
            href={'https://goo.gl/maps/eJi16maY2xHtbTQFA'}
            target="_blank"
            rel="noopener noreferrer"
            className={styles.mapButton}
          >
            Xem ch dn Google maps
          </a>
          <img src="/googlemap.png" />
        </div>
      </div>
      {user.isLoggedIn ? (
        <div className={styles.cardContainer}>
          <BikeUserInfo />
          <UploadCard />
        </div>
      ) : (
        <div className={styles.loginContainer}>
          <p>ng nhp  s dng tnh nng ny</p>
          <button onClick={() => navigate('/login')} className={styles.loginButton}>
            ng nhp
          </button>
        </div>
      )}

      {scanner ? (
        <div className={styles.scanContainer}>
          <QrReader
            delay={100}
            style={{ borderRadius: '5px' }}
            onError={handleError}
            onScan={onNewScanResult}
            facingMode={'environment'}
            legacyMode={false}
          />
        </div>
      ) : (
        <div>
          {bikeUser ? (
            // 2022-03-30T07:09:03.830+00:00
            <div className={styles.bikeUserContainer}>
              <h5>Thng tin thu xe</h5>
              <p className={styles.bikeName}>{bikeUser?.bike.name}</p>
              <p>Ngy thu: {bikeUser?.inTime.toLocaleDateString('en-GB')}</p>
              <p>Thu vo lc: {bikeUser?.inTime.toLocaleTimeString('en-GB')}</p>
              <p>Tr vo lc: {bikeUser?.outTime ? bikeUser?.outTime.toLocaleTimeString('en-GB') : 'Cha c'}</p>
              {bikeUser?.outTime ? (
                <p>
                   dng: {bikeUser?.totalDates} ngy {bikeUser?.totalHours} gi {bikeUser?.totalMinutes} pht{' '}
                </p>
              ) : null}

              {bikeUser?.state == 0 ? (
                <p className={styles.bikeWaiting}>Ch tr xe</p>
              ) : (
                <p className={styles.bikeReturned}> tr xe</p>
              )}
              {bikeUser?.state == 0 ? (
                <button onClick={handleReturnBikeButton} className={styles.loginButton}>
                  Tr xe
                </button>
              ) : null}
              {bikeUser?.totalPrice ? <p>Tng cng: {bikeUser?.totalPrice} VN</p> : null}
            </div>
          ) : (
            <div className={styles.bikeUserContainer}>
              <h5>Thng tin thu xe</h5>
              <p>Cha c thng tin thu xe</p>
            </div>
          )}
        </div>
      )}

      <div className={styles.bicycleContainer}>
        <h3 className={styles.header}>Chn xe ca bn</h3>
        <div className={styles.bicycleListContainer}>
          {bicycleList.map(child => {
            return (
              <div
                key={child._id}
                className={styles.bicycleItem}
                onClick={() => handleBicycleButtonClick(child)}
                style={child.isAvailable ? null : { color: '#ccc', borderColor: '#ccc' }}
              >
                <span>{child.name}</span>
              </div>
            )
          })}
        </div>
      </div>
    </div>
  )
}
