import React, { useState } from 'react';
import { Button } from 'react-bootstrap';
import styled from 'styled-components';
import Timeline from './Timeline';
function SearchOrder(data) {
  const {
    name,
    orderCode,
    category,
    document,
    receipt,
    state,
    isPrinted,
    instruction,
    isDelivered,
  } = data;
  const [copied, setCopied] = useState(false);
  console.log(data);
  const handleCopyButton = () => {
    navigator.clipboard.writeText(`${orderCode} ${name}`);
    setCopied(true);
  };

  return (
    <Styles>
      <p>M n hng: {orderCode}</p>
      <p>Khch hng: {name}</p>
      <p>Th loi in: {category?.name}</p>
      <p>
        Ti liu (admin):{' '}
        {typeof document === 'string' ? (
          <a href={document} rel="noopenner noreferrer" target="_blank">
            Ti liu
          </a>
        ) : (
          <div>
            {document?.map((url, index) => {
              return (
                <a href={url} rel="noopenner noreferrer" target="_blank">
                  Ti liu {index + 1} <br />
                </a>
              )
            })}
          </div>
        )}
      </p>
      <p>Hng dn in: {instruction}</p>
      <p>Trng thi: {isPrinted ? ' in' : 'Cha in'}</p>
      <p>
        Ni dung chuyn khon (khng du):
        <br />
        <b>
          {orderCode} {name}
        </b>
      </p>
      {copied ? (
        <Button
          type='button'
          variant='outline-primary'
          onClick={handleCopyButton}
        >
           chp
        </Button>
      ) : (
        <Button
          type='button'
          variant='outline-primary'
          onClick={handleCopyButton}
        >
          Sao chp
        </Button>
      )}
      <Timeline data={data?.timeline} current={state} />

      <hr />
    </Styles>
  )
}

export default SearchOrder

const Styles = styled.div``
