import React, { useState } from 'react'
import TitleBar from 'shared/components/TitleBar'
import styles from './bankPage.module.css'
import { MdOutlineContentCopy } from 'react-icons/md'

import { Swiper, SwiperSlide } from 'swiper/react/swiper-react'
// import { Pagination } from 'swiper';
// import useMediaQuery from 'hooks/useMediaQuery';

function BankPage() {
  // const isLargeScreen = useMediaQuery('min-width: 600px');
  const [step, setStep] = useState(0)
  const [copied, setCopied] = useState(0)

  const nextSlide = e => {
    const swiper = document.querySelector('.instructionSwiper').swiper
    swiper.slideNext()
  }

  const prevSlide = () => {
    const swiper = document.querySelector('.instructionSwiper').swiper
    swiper.slidePrev()
  }

  const handleSlideChange = e => {
    setStep(e.realIndex + 1)
  }

  const handleCopyButton = () => {
    try {
      navigator.clipboard.writeText('P746')
      setCopied(true)
    } catch (err) {
      alert(err)
    }
  }

  return (
    <div>
      <div className={styles.container}>
        {/* <TitleBar title="M th MB Bank" /> */}
        <img src="/bankbanner.jpg" style={{ width: '100%' }} />
        <p className={styles.header}>
          M ti khon min ph, hon ton online bng cng ngh eKYC, min ph giao dch trn i ti MBBank
        </p>
        <div className={styles.bodyContainer}>
          <h3 className={styles.mainTitle}>3 bc n gin</h3>
          <h5 className={styles.title}>Bc 1: Ti app MBBank t ca hng Play Store hoc App Store</h5>
          <a
            href={'https://play.google.com/store/apps/details?id=com.mbmobile&hl=vi&gl=US'}
            target="_blank"
            rel="noreferrer noopener"
            className={styles.button}
          >
            M CH Play
          </a>
          <a
            href={'https://apps.apple.com/vn/app/mb-bank/id1205807363?l=vi'}
            target="_blank"
            rel="noreferrer noopener"
            className={styles.button}
          >
            M App Store
          </a>
          <h5 className={styles.title}>
            Bc 2: ng app v m li bng ng dn bn di (bt buc  nhn c hon tin 30.000 ng vo ti
            khon sau khi ng k thnh cng)
          </h5>

          <a href={'https://l.linklyhq.com/l/tFVc'} target="_blank" rel="noreferrer noopener" className={styles.button}>
            M MBBank
          </a>

          <h5 className={styles.title}>
            Bc 3: Thc hin eKYC  hon tt ng k, h thng MBBank s hon tin 30.000 trong vng 5 pht sau khi
            ng k thnh cng.
          </h5>

          <Swiper
            // modules={[Pagination]}
            // pagination={{ clickable: true }}
            slidesPerView={1}
            loop={true}
            style={{ width: '100%' }}
            className="instructionSwiper"
            onSlideChange={handleSlideChange}
            autoHeight
          >
            <SwiperSlide>
              <img
                className={styles.image}
                src="/mbbank/bb1.png"
                alt="img"
                style={{ width: '100%', maxWidth: '15rem' }}
              />
            </SwiperSlide>
            <SwiperSlide>
              <img
                className={styles.image}
                src="/mbbank/bb2.png"
                alt="img"
                style={{ width: '100%', maxWidth: '15rem' }}
              />
            </SwiperSlide>
            <SwiperSlide>
              <img
                className={styles.image}
                src="/mbbank/bb3.png"
                alt="img"
                style={{ width: '100%', maxWidth: '15rem' }}
              />
            </SwiperSlide>
            <SwiperSlide>
              <img
                className={styles.image}
                src="/mbbank/bb4.png"
                alt="img"
                style={{ width: '100%', maxWidth: '15rem' }}
              />
            </SwiperSlide>
            <SwiperSlide>
              <img
                className={styles.image}
                src="/mbbank/bb5.png"
                alt="img"
                style={{ width: '100%', maxWidth: '15rem' }}
              />
            </SwiperSlide>
            <SwiperSlide>
              <img
                className={styles.image}
                src="/mbbank/bb6.png"
                alt="img"
                style={{ width: '100%', maxWidth: '15rem' }}
              />
            </SwiperSlide>
            <SwiperSlide>
              <img
                className={styles.image}
                src="/mbbank/bb7.png"
                alt="img"
                style={{ width: '100%', maxWidth: '15rem' }}
              />
            </SwiperSlide>
            <SwiperSlide>
              <img
                className={styles.image}
                src="/mbbank/bb8.png"
                alt="img"
                style={{ width: '100%', maxWidth: '15rem' }}
              />
            </SwiperSlide>
            <SwiperSlide>
              <img
                className={styles.image}
                src="/mbbank/bb9.png"
                alt="img"
                style={{ width: '100%', maxWidth: '15rem' }}
              />
            </SwiperSlide>
            <SwiperSlide>
              <img
                className={styles.image}
                src="/mbbank/bb10.png"
                alt="img"
                style={{ width: '100%', maxWidth: '15rem' }}
              />
            </SwiperSlide>
            <SwiperSlide>
              <img
                className={styles.image}
                src="/mbbank/bb11.png"
                alt="img"
                style={{ width: '100%', maxWidth: '15rem' }}
              />
            </SwiperSlide>
            <SwiperSlide>
              <img
                className={styles.image}
                src="/mbbank/bb12.png"
                alt="img"
                style={{ width: '100%', maxWidth: '15rem' }}
              />
            </SwiperSlide>
            <SwiperSlide>
              <img
                className={styles.image}
                src="/mbbank/bb13.png"
                alt="img"
                style={{ width: '100%', maxWidth: '15rem' }}
              />
            </SwiperSlide>
            <SwiperSlide>
              <img
                className={styles.image}
                src="/mbbank/bb14.png"
                alt="img"
                style={{ width: '100%', maxWidth: '15rem' }}
              />
            </SwiperSlide>
            <SwiperSlide>
              <img
                className={styles.image}
                src="/mbbank/bb15.png"
                alt="img"
                style={{ width: '100%', maxWidth: '15rem' }}
              />
            </SwiperSlide>
          </Swiper>
          <div className={styles.buttonContainer}>
            <button className={styles.slideButton} onClick={prevSlide}>
              Li li
            </button>
            <span>Bc {step}</span>
            <button className={styles.slideButton} onClick={nextSlide}>
              K tip
            </button>
          </div>
          <div className={styles.stepContainer}>
            {step === 1 ? (
              <p>
                Chn ng k ngay t mn hnh cho mng ca ng dng.
                <br />
                Lu : Bt buc m app MBBank t lin kt bn trn  nhn c hon tin 30.000
              </p>
            ) : null}
            {step === 2 ? (
              <p>Nhp S in thoi v bm Tip theo. M OTP s c gi v s in thoi bn va ng k</p>
            ) : null}
            {step === 3 ? <p>Hy nhp m OTP xc nhn</p> : null}
            {step === 4 ? (
              <p>
                Bt u eKYC bng cch chp giy t ty thn mt trc v mt sau ca bn
                <br />
                Bn c th la chn 1 trong ba giy t ty thn  xc minh thng tin: Chng minh nhn dn/Th cn cc,
                H chiu, Chng minh th qun i
              </p>
            ) : null}
            {step === 5 ? <p>Chp nh mt trc giy t ty thn</p> : null}
            {step === 6 ? <p>Chn s dng v lm tng t vi mt sau giy t ty thn</p> : null}

            {step === 7 ? (
              <p>
                Xc thc chn dung v ging ni: Xc nhn chn dung theo hng dn trong hnh v c to dy s trn mn
                hnh.
              </p>
            ) : null}
            {step === 8 ? (
              <p>
                Xc thc chn dung v ging ni: Xc nhn chn dung theo hng dn trong hnh v c to dy s trn mn
                hnh.
              </p>
            ) : null}
            {step === 9 ? (
              <p>
                Xc thc chn dung v ging ni: Xc nhn chn dung theo hng dn trong hnh v c to dy s trn mn
                hnh.
              </p>
            ) : null}
            {step === 10 ? <p>Kim tra thng tin c nhn v chn Tip tc</p> : null}
            {step === 11 ? (
              <p>
                To ti khon ng nhp (Vui lng ghi nh tn ti khon v mt khu  truy cp vo ng dng sau ny).
                Nhp m gii thiu <span style={{ color: 'red', fontWeight: 'bold' }}>P746</span>{' '}
                <MdOutlineContentCopy style={{ margin: '0 0.5rem' }} onClick={handleCopyButton} />
                <span
                  style={{
                    fontSize: '0.9rem',
                    backgroundColor: '#ccc',
                    padding: '0.2rem',
                    margin: '0 1rem 0 0',
                    borderRadius: '5px'
                  }}
                >
                  {copied ? ' chp' : null}
                </span>
                ti  M ngi gii thiu  nhn c hon tin 30.000. Sau  chn Tip tc
              </p>
            ) : null}
            {step === 12 ? (
              <p>Chn Ti khon trng vi s in thoi v chn Tip tc (s ti khon hon ton min ph)</p>
            ) : null}
            {step === 13 ? <p>Chn Tip tc khi hin th qu tng</p> : null}
            {step === 14 ? <p>Xc nhn thng tin c nhn v chn Xc nhn</p> : null}

            {step === 15 ? (
              <p>
                Nhp M OTP c gi v s in thoi v hon tt ng k. Bn s nhn c ngay 30.000 ng vo ti
                khon sau khi ng k thnh cng!
              </p>
            ) : null}
          </div>
        </div>
      </div>
    </div>
  )
}

export default BankPage
