import axios from 'axios'
import { useEffect, useState } from 'react'
import { useLocation } from 'react-router-dom'
import SearchBar from 'shared/components/SearchBar'
import styles from './registerPage.module.css'
import LazyImage from 'shared/components/LazyImage'
import PortraitBanner from 'assets/images/portrait.jpg'
import styled from 'styled-components'
import { toastWrapper } from 'utils'

import { DRIVING_LICENSE_NUMBER } from 'shared/constants/contact'
import { convertPhoneNumber } from 'utils'
import ZaloLink from 'shared/components/link/ZaloLink'

export default function DrivingRegisterPage() {
  const { search } = useLocation()
  const source = new URLSearchParams(search).get('s') || 0

  const [frontsideName, setFrontsideName] = useState(null)
  const [frontsideFile, setFrontsideFile] = useState(null)
  const [backsideName, setBacksideName] = useState(null)
  const [backsideFile, setBacksideFile] = useState(null)
  const [portraitName, setPortraitName] = useState(null)
  const [portraitFile, setPortraitFile] = useState(null)
  const [receiptName, setReceiptName] = useState(null)
  const [receiptFile, setReceiptFile] = useState(null)
  const [paymentMethod, setPaymentMethod] = useState(1)
  const [isPaid, setIsPaid] = useState(1)
  const [drivingType, setDrivingType] = useState(0)
  const [date, setDate] = useState(0)
  const [dateList, setDateList] = useState([])
  const [isLoading, setIsLoading] = useState(false)
  const [searchValue, setSearchValue] = useState('')
  const [searchData, setSearchData] = useState(null)

  const imageExtensions = ['image/jpeg', 'image/png', 'image/svg+xml', 'image/webp']

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await axios.get('/api/driving/date?formVisible=true')
        let data = response.data.data

        if (data.length > 0) {
          data = data.map(child => {
            return {
              ...child,
              date: new Date(child.date)
            }
          })

          setDateList(data)
        } else {
          toastWrapper('Cha c danh sch ngy thi mi', 'info')
        }
      } catch (e) {
        toastWrapper('Li khi ly danh sch ngy thi', 'error')
      }
    }

    fetchData()
  }, [])

  const handleSubmitButton = e => {
    setIsLoading(true)
    if (!frontsideFile) {
      setIsLoading(false)

      return toastWrapper('Li: Vui lng ti ln mt trc cmnd/cccd', 'error')
    }
    if (!backsideFile) {
      setIsLoading(false)
      return toastWrapper('Li: Vui lng ti ln mt sau cmnd/cccd', 'error')
    }
    if (!portraitFile) {
      setIsLoading(false)
      return toastWrapper('Li: Vui lng ti ln nh chn dung ca bn', 'error')
    }

    const fullname = document.getElementById('formName').value
    const tel = document.getElementById('formTel').value
    const zalo = document.getElementById('formZalo').value
    const feedback = document.getElementById('formFeedback').value
    let paidState = isPaid === 0

    if (!fullname || !tel || !zalo) {
      setIsLoading(false)
      return toastWrapper('Vui lng nhp y : h tn, s in thoi v  zalo ca bn', 'error')
    }

    const formData = new FormData()

    formData.append('name', fullname)
    formData.append('tel', tel)
    formData.append('zalo', zalo)
    formData.append('frontside', frontsideFile)
    formData.append('backside', backsideFile)
    formData.append('portrait', portraitFile)
    formData.append('receipt', receiptFile)
    formData.append('isPaid', paidState)
    formData.append('paymentMethod', paymentMethod)
    formData.append('feedback', feedback)
    formData.append('drivingType', drivingType)
    formData.append('source', source)

    if (drivingType === 0) {
      formData.append('date', dateList[date].date.getTime())
    }

    axios({
      method: 'post',
      url: '/api/driving/add',
      data: formData,
      headers: { 'Content-Type': 'multipart/form-data' }
    })
      .then(res => {
        if (res.status === 200) {
          toastWrapper(
            `ng k thnh cng. Trung tm s lin h vi bn trong thi gian sm nht. Nu bn cn h tr thm, vui lng lin h zalo: ${DRIVING_LICENSE_NUMBER} (Mr. Trung)  c x l.`,
            'success',
            { autoClose: 10000 }
          )

          document.getElementById('formName').value = ''
          document.getElementById('formTel').value = ''
          document.getElementById('formZalo').value = ''
          document.getElementById('formFeedback').value = ''

          setIsLoading(false)
        } else {
          toastWrapper(`${res.data.message}`, 'error')
          setIsLoading(false)
        }
      })
      .catch(error => {
        toastWrapper(`${error.toString()}`, 'error')
        setIsLoading(false)
      })
  }

  const handleIsPaidChange = value => {
    setIsPaid(value)
  }

  const handlePaymentMethodChange = value => {
    setPaymentMethod(value)
  }

  const handleDrivingTypeChange = value => {
    setDrivingType(value)
  }

  const handleDateChange = value => {
    setDate(value)
  }

  const uploadFrontside = e => {
    if (imageExtensions.includes(e.target.files[0].type)) {
      setFrontsideFile(e.target.files[0])
      setFrontsideName(e.target.files[0].name)
    } else {
      setFrontsideName('Li: Tp ti ln khng phi l tp hnh nh')
    }
  }
  const uploadBackside = e => {
    if (imageExtensions.includes(e.target.files[0].type)) {
      setBacksideFile(e.target.files[0])
      setBacksideName(e.target.files[0].name)
    } else {
      setBacksideName('Li: Tp ti ln khng phi l tp hnh nh')
    }
  }
  const uploadPortrait = e => {
    if (imageExtensions.includes(e.target.files[0].type)) {
      setPortraitFile(e.target.files[0])
      setPortraitName(e.target.files[0].name)
    } else {
      setPortraitName('Li: Tp ti ln khng phi l tp hnh nh')
    }
  }
  const uploadReceipt = e => {
    if (imageExtensions.includes(e.target.files[0].type)) {
      setReceiptFile(e.target.files[0])
      setReceiptName(e.target.files[0].name)
    } else {
      setReceiptName('Li: Tp ti ln khng phi l tp hnh nh')
    }
  }

  const handleSearchChange = e => {
    setSearchValue(e.target.value)
  }

  const handleSearchPress = e => {
    if (e.key === 'Enter') {
      e.preventDefault()
      axios
        .get('/api/driving/search', { params: { tel: searchValue } })
        .then(res => {
          const data = res.data.data

          if (data.length === 0) {
            toastWrapper('Khng tm thy h s khp vi s in thoi ' + searchValue, 'error')
          } else {
            setSearchData(data)
          }
        })
        .catch(e => {
          toastWrapper('Khng tm thy h s khp vi s in thoi ' + searchValue, 'error')
        })
    }
  }

  return (
    <Styles className={styles.drivingRegisterContainer}>
      <SearchBar
        placeholder={'Tra cu trng thi h s'}
        focusText={'Nhp s in thoi v nhn Enter'}
        onChange={handleSearchChange}
        onKeyPress={handleSearchPress}
        value={searchValue}
      />
      {searchData &&
        searchData.map(child => {
          const date = new Date(child.date)
          const processDate = new Date(date)
          processDate.setDate(date.getDate() - 14)
          let state = ''

          if (child.processState === 0) {
            state = 'ang ch x l'
          } else if (child.processState === 1) {
            state = 'ang ch cp nht'
          } else if (child.processState === 2) {
            state = 'ang ch thanh ton'
          } else if (child.processState === 3) {
            state = ' hon tt h s'
          } else if (child.processState === 4) {
            state = ' hy h s'
          }

          return (
            <div className={styles.orderContainer}>
              <p>H tn: {child.name}</p>
              <p>
                Ngy d thi:
                {` ${date.getDate()}/${date.getMonth() + 1}/${date.getFullYear()}`}
              </p>
              <p>Trng thi: {state}</p>
              <p>
                H s ca bn s c x l vo ngy:
                {` ${processDate.getDate()}/${processDate.getMonth() + 1}/${processDate.getFullYear()}`}
              </p>
              <p>
                Vui lng lin h{' '}
                <ZaloLink tel={DRIVING_LICENSE_NUMBER}>{convertPhoneNumber(DRIVING_LICENSE_NUMBER, '.')}</ZaloLink>
                 c h tr trong trng hp h s ca bn cha c x l.
              </p>
            </div>
          )
        })}

      <div className="portrait-rules">
        <LazyImage src={PortraitBanner} />
      </div>
      <form className={styles.drivingFormContainer}>
        <ul className="px-2">
          <li style={{ margin: 0 }}>
            Xem hng dn ng k d thi{' '}
            <a href="/driving-instruction" target="_blank" rel="noopener noreferrer">
              ti y.
            </a>
          </li>
          <li>
            Thng tin chuyn khon:
            <br />- Ch ti khon: Nguyn Ngc Hun
            <br />- Ngn hng: MB Bank (Ngn hng Qun i)
            <br />- S ti khon: 0877876877
            <br />- Ni dung: H tn_ST_Bang A1_Ngy d thi
            <br />- S tin: 590.000 ng i vi bng A1, 2.000.000 ng i vi bng A2
            <br />- Gi li nh chp bin lai trong form ng k nu  chuyn khon.
            <br />- Gi li nh chp bin lai ti{' '}
            <a href="http://zalo.me/4013961016678131109?src=qr" target="_blank" rel="noopener noreferrer">
              Zalo OA
            </a>{' '}
            nu ng sau khi ng k.
          </li>
          <li>
            Truy cp Zalo Official Account{' '}
            <a href="http://zalo.me/4013961016678131109?src=qr" target="_blank" rel="noreferrer">
              Trung tm dch v sinh vin iStudent
            </a>{' '}
            v nhn "Quan tm"  trung tm c th lin h vi bn (Quan trng)
          </li>

          <li>Hon thnh l ph thi trc ngy d thi 16 ngy.</li>
        </ul>
        <p style={{ margin: 0, color: ' #ff0000 ' }}>* bt buc</p>

        <div className={styles.formGroup}>
          <label className={styles.formLabel}>Tn ca bn*</label>
          <input
            className={styles.formInput}
            id="formName"
            type="text"
            placeholder="Nhp h tn y , c du"
            required
          />
        </div>
        <div className={styles.formGroup}>
          <label className={styles.formLabel}>S in thoi lin h*</label>
          <input
            className={styles.formInput}
            id="formTel"
            type="text"
            placeholder="Nhp s in thoi ca bn"
            required
          />
        </div>
        <div className={styles.formGroup}>
          <label className={styles.formLabel}>S in thoi Zalo*</label>
          <input className={styles.formInput} id="formZalo" type="text" placeholder="Nhp s in thoi Zalo ca bn" />
        </div>

        <div className={styles.formGroup}>
          <label className={styles.formLabel}>Mt trc CMND/CCCD*</label>
          <label className={styles.formUploadButton}>
            <input
              className={styles.formInput}
              type="file"
              accept="image/*"
              id="frontside"
              name="frontside"
              onChange={uploadFrontside}
              required
            />
            Ti tp ln
          </label>
          {frontsideName ? <p className="filename">{frontsideName}</p> : null}
        </div>
        <div className={styles.formGroup}>
          <label className={styles.formLabel}>Mt sau CMND/CCCD*</label>
          <label className={styles.formUploadButton}>
            <input
              className={styles.formInput}
              type="file"
              accept="image/*"
              id="backside"
              name="backside"
              onChange={uploadBackside}
              required
            />
            Ti tp ln
          </label>
          {backsideName ? <p className="filename">{backsideName}</p> : null}
        </div>
        <div className={styles.formGroup}>
          <label className={styles.formLabel}>
            nh chp chn dung* (khng chp nh th, xem nh mu trong hng dn{' '}
            <a href="/driving-instruction#online" target="_blank" rel="noopener noreferrer">
              ti y
            </a>
            )
          </label>
          <label className={styles.formUploadButton}>
            <input
              className={styles.formInput}
              type="file"
              accept="image/*"
              id="portrait"
              name="portrait"
              onChange={uploadPortrait}
              required
            />
            Ti tp ln
          </label>
          {portraitName ? <p className="filename">{portraitName}</p> : null}
        </div>

        <div className={styles.formGroup}>
          <label className={styles.formLabel}>Chn loi bng li*</label>
          <div className={styles.selectContainer}>
            <input
              className={styles.formInput}
              type="radio"
              onChange={() => handleDrivingTypeChange(0)}
              checked={drivingType === 0}
            />
            <p onClick={() => handleDrivingTypeChange(0)}>
              Bng A1 (M t 2 bnh c dung tch xi lanh t 50cc cho n di 175cc, l ph thi 590.000 ng, chn ngy
              thi  mc bn di)
            </p>
          </div>
          <div className={styles.selectContainer}>
            <input
              className={styles.formInput}
              type="radio"
              onChange={() => handleDrivingTypeChange(1)}
              checked={drivingType === 1}
            />
            <p onClick={() => handleDrivingTypeChange(1)}>
              Bng A2 (M t 2 bnh khng gii hn dung tch xi lanh, l ph thi 2.000.000 ng, trung tm lin h hng
              dn qua in thoi)
            </p>
          </div>
          <div className={styles.selectContainer}>
            <input
              className={styles.formInput}
              type="radio"
              onChange={() => handleDrivingTypeChange(2)}
              checked={drivingType === 2}
            />
            <p onClick={() => handleDrivingTypeChange(2)}>
              Bng B2 (Bng li xe  t, trung tm lin h hng dn qua in thoi)
            </p>
          </div>
        </div>

        {drivingType === 0 ? (
          <div className={styles.formGroup}>
            <label className={styles.formLabel}>
              Chn ngy thi (c th thay i gia 2 bui theo lch ca s GTVT)*
            </label>

            {dateList.map((child, index) => {
              return (
                <div className={styles.selectContainer} key={child._id}>
                  <input
                    className={styles.formInput}
                    type="radio"
                    onChange={() => handleDateChange(index)}
                    checked={date === index}
                  />
                  <p onClick={() => handleDateChange(index)}>{child.description || child.date.toLocaleDateString()}</p>
                </div>
              )
            })}
          </div>
        ) : null}

        <div className={styles.formGroup}>
          <label className={styles.formLabel}>Hnh thc ng l ph*</label>
          <div className={styles.selectContainer}>
            <input
              className={styles.formInput}
              type="radio"
              onChange={() => handlePaymentMethodChange(0)}
              checked={paymentMethod === 0}
            />
            <p onClick={() => handlePaymentMethodChange(0)}>ng trc tip ti nh khch HQG</p>
          </div>
          <div className={styles.selectContainer}>
            <input
              className={styles.formInput}
              type="radio"
              onChange={() => handlePaymentMethodChange(1)}
              checked={paymentMethod === 1}
            />
            <p onClick={() => handlePaymentMethodChange(1)}>Chuyn khon ngn hng</p>
          </div>
        </div>
        <div className={styles.formGroup}>
          <label className={styles.formLabel}>Bn  ng l ph cha?*</label>
          <div className={styles.selectContainer}>
            <input
              className={styles.formInput}
              type="radio"
              onChange={() => handleIsPaidChange(0)}
              checked={isPaid === 0}
            />
            <p onClick={() => handleIsPaidChange(0)}> thanh ton</p>
          </div>

          <div className={styles.selectContainer}>
            <input
              className={styles.formInput}
              type="radio"
              onChange={() => handleIsPaidChange(1)}
              checked={isPaid === 1}
            />
            <p onClick={() => handleIsPaidChange(1)}>Mnh s ng l ph sau</p>
          </div>
        </div>
        <div className={styles.formGroup}>
          <label className={styles.formLabel}>nh chp bin lai (nu  ng l ph)</label>
          <label className={styles.formUploadButton}>
            <input
              className={styles.formInput}
              type="file"
              accept="image/*"
              id="receipt"
              name="receipt"
              onChange={uploadReceipt}
            />
            Ti tp ln
          </label>

          {receiptName ? <p className={styles.formFilename}>{receiptName}</p> : null}
        </div>
        <div className={styles.formGroup}>
          <label className={styles.formLabel}>Thc mc/Gp </label>
          <textarea className={styles.formFeedbackInput} id="formFeedback" />
        </div>

        {isLoading ? (
          <>
            <p>H thng ang x l, vui lng ch trong t nht 15 giy</p>
            <p className={styles.formSubmitButton}>ang ng k...</p>
          </>
        ) : (
          <button type="button" onClick={handleSubmitButton} className={styles.formSubmitButton}>
            ng k
          </button>
        )}
        <p style={{ margin: '1rem 0' }}>
          Trong qu trnh ng k, nu xy ra li h thng, vui lng chp mn hnh li gi v Zalo:{' '}
          <a href="https://zalo.me/0797324886" target="_blank" rel="noopener noreferrer">
            0797324886
          </a>{' '}
           c h tr nhanh nht.
        </p>
      </form>
    </Styles>
  )
}

const Styles = styled.div`
  .portrait-rules {
    margin: 0 auto;
    width: 95%;
    border-radius: 0.5rem;
    overflow: hidden;
  }

  .filename {
    margin: 1rem 0 0;
    font-weight: bold;
  }
`
