import axios from 'axios'
import { useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { selectUser, updateName, updateTel, updateZalo } from 'store/userSlice'
import styles from '../swimmingPoolTicketPage.module.css'

export default function TutorForm(props) {
  const dispatch = useDispatch()
  let userInfo = useSelector(selectUser)
  const [isLoading, setIsLoading] = useState(false)

  const handleSubmitButton = e => {
    setIsLoading(true)
    const name = document.getElementById('formName').value
    const tel = document.getElementById('formTel').value
    const zalo = document.getElementById('formZalo').value
    const feedback = document.getElementById('formFeedback').value

    if (!name || !tel || !zalo) {
      setIsLoading(false)
      return alert('Vui lng nhp y  cc trng H tn, s in thoi v zalo!')
    }

    axios({
      method: 'post',
      url: '/api/pool/tutor-user',
      data: { name, tel, zalo, feedback }
    })
      .then(res => {
        if (res.status === 200) {
          alert(
            'ng k thnh cng. Trung tm s lin h vi bn trong thi gian sm nht. Nu bn cn h tr thm, vui lng lin h zalo  c x l.'
          )
          setIsLoading(false)
        } else {
          alert('Li: ' + res.data.message)
          setIsLoading(false)
        }
      })
      .catch(error => {
        console.log(error)
        alert('Li: ' + error)
        setIsLoading(false)
      })
  }

  const handleNameChange = e => {
    dispatch(updateName(e.target.value))
  }

  const handleTelChange = e => {
    dispatch(updateTel(e.target.value))
  }

  const handleZaloChange = e => {
    dispatch(updateZalo(e.target.value))
  }

  return (
    <form style={props.display === false ? { display: 'none' } : null}>
      <ul style={{ listStyle: 'none', padding: '0' }}>
        <li>
          <p>Hc ph: 600.000 ng/1 k (6 bui).</p>
          <p>Hc ph ton kha: 1.200.000 ng/2 k (12 bui) bao bit bi, min ph v vo h bi.</p>
        </li>
        <li>
          <p>Gim 10% khi ng k theo nhm 5 ngi.</p>
        </li>
        <li>
          <p>Gim 15% khi ng k theo nhm 10 ngi.</p>
        </li>
        <li>
          <p>C th ng hc ph theo 1 k hoc ton kha.</p>
        </li>
        <li>
          Lin h t vn:{' '}
          <a href="https://zalo.me/0877876877" target="_blank" rel="noopener noreferrer">
            0877.876.877
          </a>
        </li>
      </ul>
      <p style={{ margin: 0, color: ' #ff0000 ' }}>* bt buc</p>

      <div className={styles.formGroup}>
        <label className={styles.formLabel}>Tn ca bn*</label>
        <input
          className={styles.formInput}
          id="formName"
          type="text"
          placeholder="Nhp h tn y , c du"
          required
          value={userInfo.data.name}
          onChange={handleNameChange}
        />
      </div>
      <div className={styles.formGroup}>
        <label className={styles.formLabel}>S in thoi lin h*</label>
        <input
          className={styles.formInput}
          id="formTel"
          type="text"
          placeholder="Nhp s in thoi ca bn"
          required
          value={userInfo.data.tel}
          onChange={handleTelChange}
        />
      </div>
      <div className={styles.formGroup}>
        <label className={styles.formLabel}>S in thoi Zalo*</label>
        <input
          className={styles.formInput}
          id="formZalo"
          type="text"
          placeholder="Nhp s in thoi Zalo ca bn"
          required
          value={userInfo.data.zalo}
          onChange={handleZaloChange}
        />
      </div>

      <div className={styles.formGroup}>
        <label className={styles.formLabel}>Thc mc/Gp </label>
        <textarea className={styles.formFeedbackInput} id="formFeedback" />
      </div>

      {isLoading ? (
        <>
          <p>H thng ang x l, vui lng ch trong t nht 15 giy {'<3'}</p>
          <p className={styles.formSubmitButton}>ang ng k...</p>
        </>
      ) : (
        <button type="button" onClick={handleSubmitButton} className={styles.formSubmitButton}>
          ng k
        </button>
      )}
      <p style={{ margin: '1rem 0' }}>
        Trong qu trnh ng k, nu xy ra li h thng, vui lng chp mn hnh li gi v Zalo:{' '}
        <a href="href='https://zalo.me/0797324886" target="_blank" rel="noopener noreferrer">
          0797324886
        </a>{' '}
         c h tr nhanh nht. Xin cm n.
      </p>
    </form>
  )
}
