import axios from 'axios'
import React, { useEffect, useState } from 'react'
import { authHeader } from 'utils'

import VoucherCardVertical from './VoucherCardVertical'

import styles from './voucherList.module.css'
function VoucherListVertical(props) {
  const [data, setData] = useState([])

  useEffect(() => {
    axios
      .get('/api/coupon-user', authHeader())
      .then(res => {
        setData(res.data.data)
      })
      .catch(e => {
        console.log(e)
        alert('Khng th ti danh sch u i ca bn. Vui lng lin h admin  c h tr nhanh nht')
      })
  }, [])

  return (
    <div className={styles.voucherList}>
      <div className={styles.listVertical}>
        {data.map(child => {
          console.log(child)
          return <VoucherCardVertical key={child._id} coupon={child.coupon} isUsed={child.isUsed ? 1 : 0} />
        })}
      </div>
    </div>
  )
}

export default VoucherListVertical
