import guideApi from 'api/guideApi'
import { convertToRaw, EditorState } from 'draft-js'
import draftToHtml from 'draftjs-to-html'
import { useState } from 'react'
import { Editor } from 'react-draft-wysiwyg'
import 'react-draft-wysiwyg/dist/react-draft-wysiwyg.css'
import { toastWrapper } from 'utils'
import styles from './creation.module.css'

function Creation() {
  const [editorState, setEditorState] = useState(EditorState.createEmpty())
  const hashtagConfig = {
    trigger: '#',
    separator: ' '
  }

  const onEditorStateChange = editorState => {
    setEditorState(editorState)
  }

  const onSubmit = e => {
    e.preventDefault()
    const rawContentState = convertToRaw(editorState.getCurrentContent())
    const content = draftToHtml(rawContentState, hashtagConfig, true)
    const title = document.getElementById('formTitle').value
    const priority = parseInt(document.getElementById('formPriority').value)

    if (!title) {
      return alert('Vui lng nhp y  tiu  v ni dung bi vit!')
    }

    if (!priority || priority < 0) {
      return alert(' u tin phi l s ln hn 0!')
    }

    guideApi
      .createGuide({ priority, title, content })
      .then(res => toastWrapper('To bi vit thnh cng!', 'success'))
      .catch(err => console.log(err))
  }

  return (
    <div className={styles.container}>
      <form onSubmit={onSubmit}>
        <div className={styles.formGroup}>
          <label className={styles.formLabel}> u tin</label>
          <input className={styles.formInput} id="formPriority" placeholder="Nhp s ln hn 0" />
        </div>
        <div className={styles.formGroup}>
          <label className={styles.formLabel}>Tiu </label>
          <input className={styles.formInput} id="formTitle" placeholder="Nhp tiu  bi vit" />
        </div>
        <label className={styles.formLabel}>Ni dung</label>
        <div className={styles.editorContainer}>
          <Editor
            editorState={editorState}
            toolbarClassName="toolbarClassName"
            wrapperClassName="wrapperClassName"
            editorClassName="editorClassName"
            onEditorStateChange={onEditorStateChange}
          />
        </div>
        <input className={styles.submitButton} type="submit" value={'To bi vit'} />
      </form>
    </div>
  )
}

export default Creation
