import axios from 'axios'
import React, { useEffect, useState } from 'react'
import styles from './creation.module.css'

function Creation() {
  const [category, setCategory] = useState([])

  useEffect(() => {
    axios
      .get('/api/guest-house/category', {
        params: { page: 0, limit: 10 }
      })
      .then(res => {
        setCategory(res.data.data)
      })
      .catch(err => alert(err.toString))
  }, [])

  const handleSubmitButton = () => {
    const number = document.getElementById('formNumber').value
    const description = document.getElementById('formDesc').value
    const category = document.getElementById('formCategory').value
    const dailyPrice = document.getElementById('formDailyPrice').value
    const monthlyPrice = document.getElementById('formMonthlyPrice').value
    const minQuantity = document.getElementById('formMinQuantity').value
    const maxQuantity = document.getElementById('formMaxQuantity').value

    if (!number || !description || !category || !dailyPrice || !monthlyPrice || !minQuantity || !maxQuantity) {
      return alert('Vui lng nhp y  cc trng!')
    }

    if (isNaN(number)) {
      return alert('S phng khng hp l')
    }

    if (isNaN(dailyPrice)) {
      return alert('Gi ngy khng hp l')
    }

    if (isNaN(monthlyPrice)) {
      return alert('Gi thng khng hp l')
    }

    if (isNaN(minQuantity)) {
      return alert('S lng ti thiu khng hp l')
    }

    if (isNaN(maxQuantity)) {
      return alert('S lng ti a khng hp l')
    }

    const confirmed = window.confirm(`Bn c chc chn to phng ${number} vi cc thng tin  cung cp khng?`)

    if (confirmed) {
      axios
        .post('/api/guest-house/room', {
          number,
          description,
          category,
          dailyPrice,
          monthlyPrice,
          minQuantity,
          maxQuantity
        })
        .then(res => {
          return alert('To phng thnh cng!')
        })
        .catch(err => {
          console.log(err)
          return alert(err.toString())
        })
    }
  }

  return (
    <div className={styles.container}>
      <div className={styles.formGroup}>
        <label className={styles.formLabel}>S phng</label>
        <input className={styles.formInput} type="text" id="formNumber" />
      </div>
      <div className={styles.formGroup}>
        <label className={styles.formLabel}>M t</label>
        <textarea className={styles.formInput} id="formDesc"></textarea>
      </div>
      <div className={styles.formGroup}>
        <label className={styles.formLabel}>Loi phng</label>
        <select id="formCategory">
          {category.map(child => {
            return <option value={child._id}>{child.name}</option>
          })}
        </select>
      </div>
      <div className={styles.formGroup}>
        <label className={styles.formLabel}>Gi ngy</label>
        <input className={styles.formInput} type="text" id="formDailyPrice" />
      </div>
      <div className={styles.formGroup}>
        <label className={styles.formLabel}>Gi thng</label>
        <input className={styles.formInput} type="text" id="formMonthlyPrice" />
      </div>
      <div className={styles.formGroup}>
        <label className={styles.formLabel}>S lng ti thiu</label>
        <input className={styles.formInput} type="text" id="formMinQuantity" />
      </div>
      <div className={styles.formGroup}>
        <label className={styles.formLabel}>S lng ti a</label>
        <input className={styles.formInput} type="text" id="formMaxQuantity" />
      </div>
      <button className={styles.formButton} onClick={handleSubmitButton}>
        To phng
      </button>
    </div>
  )
}

export default Creation
