import busApi from 'api/busApi'
import TitleBar from 'shared/components/TitleBar'
import { useState } from 'react'
import { Button, Form } from 'react-bootstrap'
import { useForm } from 'react-hook-form'
import styled from 'styled-components'
import InputField from './components/InputField'
import Content from './components/Content'

export function BusRegistrationPage(props) {
  const [isLoading, setIsLoading] = useState(false)
  const {
    register,
    handleSubmit,
    formState: { errors }
  } = useForm()

  const handleSubmitButton = e => {
    setIsLoading(true)

    busApi
      .createBusUser({
        name: e.name,
        tel: e.tel,
        zalo: e.zalo,
        note: e.note,
        address: e.address
      })
      .then(res => {
        console.log(res)
        if (res.data) {
          setIsLoading(false)
          return alert(
            'ng k thnh cng! Chng mnh s lin h vi bn trong thi gian sm nht. Mi thc mc lin h 0877.876.877  c gii p. Xin cm n!'
          )
        }
      })
      .catch(e => {
        console.log(e)
        setIsLoading(false)
      })
  }

  return (
    <Styles>
      {/* <TitleBar title="t xe a rc" navigation="/uniforms" /> */}

      <Form onSubmit={handleSubmit(handleSubmitButton)} className="drivingFormContainer">
        <Content />
        <InputField
          register={register}
          name={'name'}
          label="Tn ca bn*"
          placeholder="Nhp y  h tn, c du"
          errors={errors}
          require={true}
        ></InputField>
        <InputField
          register={register}
          name={'tel'}
          label=" in thoi lin h*"
          placeholder="Nhp s in thoi lin h ca bn"
          require={true}
        ></InputField>
        <InputField
          register={register}
          name={'zalo'}
          label="S in thoi Zalo (Khng bt buc)"
          placeholder="Nhp s in thoi zalo ca bn"
          errors={errors}
        ></InputField>
        <InputField
          register={register}
          name={'address'}
          label="a ch n bn (Khng bt buc)"
          placeholder="B trng nu cn xc nhn li sau"
          errors={errors}
        ></InputField>
        <Form.Group className="mb-3" controlId="exampleForm.ControlTextarea1">
          <Form.Label>Yu cu khc/Ghi ch (nu c)</Form.Label>
          <Form.Control {...register('note')} as="textarea" rows={3} placeholder="Nhp yu cu ca bn nu c" />
        </Form.Group>
        {isLoading ? (
          <Form.Group className="align-self-center">
            <Button variant="primary" type="submit" className="px-3" disabled>
              ang ng k...
            </Button>
          </Form.Group>
        ) : (
          <Form.Group className="align-self-center">
            <Button variant="primary" type="submit" className="px-3">
              ng k
            </Button>
          </Form.Group>
        )}
      </Form>
    </Styles>
  )
}
const Styles = styled.div`
  .drivingFormContainer {
    width: 95%;
    margin: 1rem auto;
    padding: 1rem 1rem 5rem;
    border-radius: 1rem;
    background-color: var(--white);
    display: flex;
    flex-direction: column;
  }
`
