import axios from 'axios'
import TitleBar from 'shared/components/TitleBar'
import { useState } from 'react'
import Autosuggest from 'react-autosuggest'
import { useDispatch, useSelector } from 'react-redux'
import {
  selectBusSurvey,
  updateAddress,
  updateDistrict,
  updateIntime,
  updateIsUsed,
  updateOuttime,
  updateProvince,
  updateReason,
  updateSchool,
  updateTown
} from 'store/busSurveySlice'
import { selectUser, updateEmail, updateName, updateTel } from 'store/userSlice'
import styles from './styles.module.css'

export function BusSurveyPage() {
  const schoolList = [
    'Trng i hc Bch Khoa (c s 2)',
    'Trng i hc Bch Khoa (c s 1)',
    'Trng i hc Khoa hc T nhin (c s 2)',
    'Trng i hc Khoa hc T nhin (c s 1)',
    'Trng i hc Khoa hc X hi v Nhn vn (c s 2)',
    'Trng i hc Khoa hc X hi v Nhn vn (c s 1)',
    'Trng i hc Quc t',
    'Trng i hc Cng ngh Thng tin',
    'Trng i hc Kinh t - Lut ',
    'Vin Mi trng - Ti nguyn'
  ]

  const dispatch = useDispatch()
  const user = useSelector(selectUser)

  const busSurvey = useSelector(selectBusSurvey)
  const [isLoading, setIsLoading] = useState(false)
  const [currentDay, setCurrentDay] = useState(0)
  const [suggestions, setSuggestions] = useState([])
  let currentDayText = ''

  switch (currentDay) {
    case 0:
      currentDayText = 'Th 2'
      break
    case 1:
      currentDayText = 'Th 3'
      break
    case 2:
      currentDayText = 'Th 4'
      break
    case 3:
      currentDayText = 'Th 5'
      break
    case 4:
      currentDayText = 'Th 6'
      break
    case 5:
      currentDayText = 'Th 7'
      break
    case 6:
      currentDayText = 'Ch nht'
      break
    default:
      currentDayText = ''
  }

  const imageExtensions = ['image/jpeg', 'image/png', 'image/svg+xml', 'image/webp']

  const handleSubmitButton = e => {
    const fullname = document.getElementById('formName').value
    const tel = document.getElementById('formTel').value
    const email = document.getElementById('formEmail').value
    const address = document.getElementById('formAddress').value
    const town = document.getElementById('formTown').value
    const district = document.getElementById('formDistrict').value
    const province = document.getElementById('formProvince').value
    const feedback = document.getElementById('formFeedback').value

    if (!fullname || !tel) {
      return alert('Vui lng nhp y : h tn v s in thoi')
    }

    if (!address || !town || !district || !province) {
      return alert('Vui lng nhp y : a ch n bn')
    }

    if (!busSurvey.school) {
      return alert('Vui lng nhp y : tn trng ca bn')
    }

    if (!busSurvey.datetime) {
      return alert('Vui lng chn gi i v gi v cho cc ngy trong tun')
    }

    const formData = new FormData()

    for (let child of busSurvey.datetime) {
      formData.append('datetime', JSON.stringify(child))
    }

    formData.append('name', fullname)
    formData.append('tel', tel)
    formData.append('email', email)
    formData.append('address', address)
    formData.append('town', busSurvey.town)
    formData.append('district', busSurvey.district)
    formData.append('province', busSurvey.province)
    formData.append('feedback', feedback)
    formData.append('school', busSurvey.school)
    formData.append('reason', busSurvey.reason)
    formData.append('isUsed', busSurvey.isUsed)

    axios({
      method: 'post',
      url: '/api/bus-survey',
      data: formData,
      headers: { 'Content-Type': 'multipart/form-data' }
    })
      .then(res => {
        if (res.status === 200) {
          alert(
            'Cm n bn  hon thnh kho st. Chng ti s gi thng bo qu tng n a ch email bn ng k sau khi kt thc t kho st ny (d kin: 30/5/2022)'
          )
          setIsLoading(false)
        } else {
          alert('Li: ' + res.data.message)
          setIsLoading(false)
        }
      })
      .catch(error => {
        console.log(error)
        alert('Li: ' + error)
        setIsLoading(false)
      })
  }

  const handleReasonChange = index => {
    dispatch(updateReason(index))
  }

  const handleIsUsedChange = value => {
    dispatch(updateIsUsed(value))
  }

  const handleDayChange = (e, value) => {
    e.preventDefault()
    setCurrentDay(value)
  }

  const handleInTimeChange = (e, value) => {
    let time = [...busSurvey.datetime[currentDay].inTime]

    if (e.target.checked) {
      if (time.includes(value)) {
        return
      }

      time.push(value)
      time.sort((a, b) => a - b)
      dispatch(updateIntime({ day: currentDay, time }))
    } else {
      if (time.includes(value)) {
        const index = time.indexOf(value)
        time.splice(index, 1)
        dispatch(updateIntime({ day: currentDay, time }))
      }
    }
  }
  const handleOutTimeChange = (e, value) => {
    let time = [...busSurvey.datetime[currentDay].outTime]

    if (e.target.checked) {
      if (time.includes(value)) {
        return
      }

      time.push(value)
      time.sort((a, b) => a - b)
      dispatch(updateOuttime({ day: currentDay, time }))
    } else {
      if (time.includes(value)) {
        const index = time.indexOf(value)
        time.splice(index, 1)
        dispatch(updateOuttime({ day: currentDay, time }))
      }
    }
  }

  const handleNameChange = e => {
    dispatch(updateName(e.target.value))
  }

  const handleEmailChange = e => {
    dispatch(updateEmail(e.target.value))
  }

  const handleTelChange = e => {
    dispatch(updateTel(e.target.value))
  }

  const handleAddressChange = e => {
    dispatch(updateAddress(e.target.value))
  }

  const handleTownChange = e => {
    dispatch(updateTown(e.target.value))
  }

  const handleDistrictChange = e => {
    dispatch(updateDistrict(e.target.value))
  }

  const handleProvinceChange = e => {
    dispatch(updateProvince(e.target.value))
  }

  const getSuggestions = value => {
    const inputValue = value.trim().toLowerCase()
    const inputLength = inputValue.length

    if (inputLength === 0) return []

    let filterList = schoolList.filter(suggestion => suggestion.toLowerCase().indexOf(inputValue.toLowerCase()) > -1)

    if (!filterList || filterList.length < 1) {
      return ['Trng khc']
    }

    return filterList
  }

  const onSuggestionsFetchRequested = ({ value }) => {
    setSuggestions(getSuggestions(value))
  }

  const onSuggestionsClearRequested = () => {
    setSuggestions([])
  }

  const onChange = (event, { newValue }) => {
    dispatch(updateSchool(newValue))
  }

  const inputProps = {
    placeholder: 'Chn trng t gi  hoc nhp tn trng',
    value: busSurvey.school,
    onChange: onChange
  }

  const getSuggestionValue = suggestion => suggestion

  const renderSuggestion = suggestion => <div>{suggestion}</div>

  return (
    <div className={styles.drivingRegisterContainer}>
      <TitleBar title="Kho st nhu cu xe but" />
      <form className={styles.drivingFormContainer}>
        <p style={{ margin: 0, color: ' #ff0000 ' }}>* bt buc</p>

        <div className={styles.formGroup}>
          <label className={styles.formLabel}>Tn ca bn*</label>
          <input
            className={styles.formInput}
            id="formName"
            type="text"
            placeholder="Nhp h tn y , c du"
            value={user.data.name}
            onChange={handleNameChange}
          />
        </div>
        <div className={styles.formGroup}>
          <label className={styles.formLabel}>S in thoi lin h*</label>
          <input
            className={styles.formInput}
            id="formTel"
            type="text"
            placeholder="Nhp s in thoi ca bn"
            onChange={handleTelChange}
            value={user.data.tel}
          />
        </div>

        <div className={styles.formGroup}>
          <label className={styles.formLabel}>a ch email (khng bt buc)</label>
          <input
            className={styles.formInput}
            id="formEmail"
            type="text"
            placeholder="Nhp a ch email  nhn voucher"
            onChange={handleEmailChange}
            value={user.data.email}
          />
        </div>

        <div className={styles.formGroup}>
          <label className={styles.formLabel}>
            Bn c mong mun c s dng dch v xe a n t nh n trng v ngc li khng (dch v Shuttle Bus:
            Chn ch v thanh ton online; Bit v tr xe bus; Khng cng thng khi phi t iu khin xe; An ton, bo
            v mi trng...)?*
          </label>
          <div className={styles.selectContainer}>
            <input
              className={styles.formInput}
              type="radio"
              onChange={() => handleIsUsedChange(true)}
              checked={busSurvey?.isUsed === true}
            />
            <p onClick={() => handleIsUsedChange(true)}>C</p>
          </div>
          <div className={styles.selectContainer}>
            <input
              className={styles.formInput}
              type="radio"
              onChange={() => handleIsUsedChange(false)}
              checked={busSurvey?.isUsed === false}
            />
            <p onClick={() => handleIsUsedChange(false)}>Khng</p>
          </div>
        </div>

        <div className={styles.formGroup}>
          <label className={styles.formLabel}>u l l do quan trng nht khi bn chn dch v xe a n ny*</label>
          <div className={styles.selectContainer}>
            <input
              className={styles.formInput}
              type="radio"
              onChange={() => handleReasonChange(0)}
              checked={busSurvey?.reason === 0}
            />
            <p onClick={() => handleReasonChange(0)}>Khng c xe c nhn</p>
          </div>
          <div className={styles.selectContainer}>
            <input
              className={styles.formInput}
              type="radio"
              onChange={() => handleReasonChange(1)}
              checked={busSurvey?.reason === 1}
            />
            <p onClick={() => handleReasonChange(1)}>Khng mun t iu khin xe c nhn</p>
          </div>
          <div className={styles.selectContainer}>
            <input
              className={styles.formInput}
              type="radio"
              onChange={() => handleReasonChange(2)}
              checked={busSurvey?.reason === 2}
            />
            <p onClick={() => handleReasonChange(2)}>Khong cch t nh n trng qu xa</p>
          </div>
          <div className={styles.selectContainer}>
            <input
              className={styles.formInput}
              type="radio"
              onChange={() => handleReasonChange(3)}
              checked={busSurvey?.reason === 3}
            />
            <p onClick={() => handleReasonChange(3)}>Bo v mi trng</p>
          </div>
          <div className={styles.selectContainer}>
            <input
              className={styles.formInput}
              type="radio"
              onChange={() => handleReasonChange(4)}
              checked={busSurvey?.reason === 4}
            />
            <p onClick={() => handleReasonChange(4)}>L do khc</p>
          </div>
        </div>
        <div className={styles.formGroup}>
          <label className={styles.formLabel} style={{ color: 'red' }}>
            Chn ln lt <strong>gi n trng (gi c mt ti trng d kin)</strong> v <strong>gi v</strong> cho
            cc ngy t Th Hai n Ch Nht (c th b trng cc ngy khng c nhu cu)
          </label>
          <div>
            <button
              className={currentDay === 0 ? styles.selectedDay : styles.dayOfWeek}
              onClick={e => handleDayChange(e, 0)}
            >
              Th 2
            </button>
            <button
              className={currentDay === 1 ? styles.selectedDay : styles.dayOfWeek}
              onClick={e => handleDayChange(e, 1)}
            >
              Th 3
            </button>
            <button
              className={currentDay === 2 ? styles.selectedDay : styles.dayOfWeek}
              onClick={e => handleDayChange(e, 2)}
            >
              Th 4
            </button>
            <button
              className={currentDay === 3 ? styles.selectedDay : styles.dayOfWeek}
              onClick={e => handleDayChange(e, 3)}
            >
              Th 5
            </button>
            <button
              className={currentDay === 4 ? styles.selectedDay : styles.dayOfWeek}
              onClick={e => handleDayChange(e, 4)}
            >
              Th 6
            </button>
            <button
              className={currentDay === 5 ? styles.selectedDay : styles.dayOfWeek}
              onClick={e => handleDayChange(e, 5)}
            >
              Th 7
            </button>
            <button
              className={currentDay === 6 ? styles.selectedDay : styles.dayOfWeek}
              onClick={e => handleDayChange(e, 6)}
            >
              Ch nht
            </button>
          </div>
        </div>

        <div className={styles.formGroup}>
          <label className={styles.formLabel}>
            Gi n trng (gi c mt ti trng d kin) ({currentDayText}) *
          </label>
          <div className={styles.inTimeContainer}>
            <div className={styles.boxGroup}>
              <input
                type="checkbox"
                name="inTime"
                checked={busSurvey.datetime[currentDay].inTime.includes(6)}
                onChange={e => handleInTimeChange(e, 6)}
              />
              <label for="inTime">6:00</label>
            </div>
            <div className={styles.boxGroup}>
              <input
                type="checkbox"
                name="inTime"
                checked={busSurvey.datetime[currentDay].inTime.includes(7)}
                onChange={e => handleInTimeChange(e, 7)}
              />
              <label for="inTime">7:00</label>
            </div>
            <div className={styles.boxGroup}>
              <input
                type="checkbox"
                name="inTime"
                checked={busSurvey.datetime[currentDay].inTime.includes(8)}
                onChange={e => handleInTimeChange(e, 8)}
              />
              <label for="inTime">8:00</label>
            </div>
            <div className={styles.boxGroup}>
              <input
                type="checkbox"
                name="inTime"
                checked={busSurvey.datetime[currentDay].inTime.includes(9)}
                onChange={e => handleInTimeChange(e, 9)}
              />
              <label for="inTime">9:00</label>
            </div>
            <div className={styles.boxGroup}>
              <input
                type="checkbox"
                name="inTime"
                checked={busSurvey.datetime[currentDay].inTime.includes(13)}
                onChange={e => handleInTimeChange(e, 13)}
              />
              <label for="inTime">13:00</label>
            </div>
            <div className={styles.boxGroup}>
              <input
                type="checkbox"
                name="inTime"
                checked={busSurvey.datetime[currentDay].inTime.includes(14)}
                onChange={e => handleInTimeChange(e, 14)}
              />
              <label for="inTime">14:00</label>
            </div>
            <div className={styles.boxGroup}>
              <input
                type="checkbox"
                name="inTime"
                checked={busSurvey.datetime[currentDay].inTime.includes(15)}
                onChange={e => handleInTimeChange(e, 15)}
              />
              <label for="inTime">15:00</label>
            </div>
            <div className={styles.boxGroup}>
              <input
                type="checkbox"
                name="inTime"
                checked={busSurvey.datetime[currentDay].inTime.includes(16)}
                onChange={e => handleInTimeChange(e, 16)}
              />
              <label for="inTime">16:00</label>
            </div>
            <div className={styles.boxGroup}>
              <input
                type="checkbox"
                name="inTime"
                checked={busSurvey.datetime[currentDay].inTime.includes(-1)}
                onChange={e => handleInTimeChange(e, -1)}
              />
              <label for="inTime">Gi khc</label>
            </div>
          </div>
        </div>

        <div className={styles.formGroup}>
          <label className={styles.formLabel}>Gi v ({currentDayText}) *</label>
          <div className={styles.inTimeContainer}>
            <div className={styles.boxGroup}>
              <input
                type="checkbox"
                name="outTime"
                checked={busSurvey.datetime[currentDay].outTime.includes(9)}
                onChange={e => handleOutTimeChange(e, 9)}
              />
              <label for="outTime">9:00</label>
            </div>
            <div className={styles.boxGroup}>
              <input
                type="checkbox"
                name="outTime"
                checked={busSurvey.datetime[currentDay].outTime.includes(10)}
                onChange={e => handleOutTimeChange(e, 10)}
              />
              <label for="outTime">10:00</label>
            </div>
            <div className={styles.boxGroup}>
              <input
                type="checkbox"
                name="outTime"
                checked={busSurvey.datetime[currentDay].outTime.includes(11)}
                onChange={e => handleOutTimeChange(e, 11)}
              />
              <label for="outTime">11:00</label>
            </div>
            <div className={styles.boxGroup}>
              <input
                type="checkbox"
                name="outTime"
                checked={busSurvey.datetime[currentDay].outTime.includes(15)}
                onChange={e => handleOutTimeChange(e, 15)}
              />
              <label for="outTime">15:00</label>
            </div>
            <div className={styles.boxGroup}>
              <input
                type="checkbox"
                name="outTime"
                checked={busSurvey.datetime[currentDay].outTime.includes(16)}
                onChange={e => handleOutTimeChange(e, 16)}
              />
              <label for="outTime">16:00</label>
            </div>
            <div className={styles.boxGroup}>
              <input
                type="checkbox"
                name="outTime"
                checked={busSurvey.datetime[currentDay].outTime.includes(17)}
                onChange={e => handleOutTimeChange(e, 17)}
              />
              <label for="outTime">17:00</label>
            </div>
            <div className={styles.boxGroup}>
              <input
                type="checkbox"
                name="outTime"
                checked={busSurvey.datetime[currentDay].outTime.includes(-1)}
                onChange={e => handleOutTimeChange(e, -1)}
              />
              <label for="outTime">Gi khc</label>
            </div>
          </div>
        </div>

        <div className={styles.formGroup}>
          <label className={styles.formLabel}>a ch n bn (ch nhp s nh v ng hoc KTX ang )*</label>
          <input
            className={styles.formInput}
            id="formAddress"
            type="text"
            placeholder="VD: 01 Nguyn Tt Thnh"
            value={busSurvey?.address}
            onChange={handleAddressChange}
          />
        </div>

        <div className={styles.formGroup}>
          <label className={styles.formLabel}>Phng/X*</label>
          <input
            className={styles.formInput}
            id="formTown"
            type="text"
            placeholder="VD: Phng 12"
            value={busSurvey?.town}
            onChange={handleTownChange}
          />
        </div>

        <div className={styles.formGroup}>
          <label className={styles.formLabel}>Qun/Huyn*</label>
          <input
            className={styles.formInput}
            id="formDistrict"
            type="text"
            placeholder="VD: Qun 4"
            value={busSurvey?.district}
            onChange={handleDistrictChange}
          />
          {/* <select
            defaultValue={busSurvey.district}
            onChange={handleDistrictChange}
          >
            <option value={0}>Thnh ph Th c</option>
            <option value={1}>Qun 1</option>
            <option value={3}>Qun 3</option>
            <option value={4}>Qun 4</option>
            <option value={5}>Qun 5</option>
            <option value={6}>Qun 6</option>
            <option value={7}>Qun 7</option>
            <option value={8}>Qun 8</option>
            <option value={10}>Qun 10</option>
            <option value={11}>Qun 11</option>
            <option value={12}>Qun 12</option>
            <option value={13}>Qun Bnh Thnh</option>
            <option value={14}>Qun Bnh Tn</option>
            <option value={15}>Qun G Vp</option>
            <option value={16}>Qun Ph Nhun</option>
            <option value={17}>Qun Tn Bnh</option>
            <option value={18}>Qun Tn Ph</option>
            <option value={19}>Khu vc khc</option>
          </select> */}
        </div>

        <div className={styles.formGroup}>
          <label className={styles.formLabel}>Tnh/Thnh ph*</label>
          <input
            className={styles.formInput}
            id="formProvince"
            type="text"
            placeholder="VD: H Ch Minh"
            value={busSurvey?.province}
            onChange={handleProvinceChange}
          />
        </div>

        <div className={styles.formGroup}>
          <label className={styles.formLabel}>Vui lng chn trng bn mun n*</label>
          {/* <input
            className={styles.formInput}
            id='formSchool'
            type='text'
            placeholder='Nhp tn trng v c s'
            value={busSurvey?.school}
            onChange={handleSchoolChange}
          /> */}
          <Autosuggest
            theme={{
              container: styles.formSchool__container,
              input: styles.formSchool__input,
              suggestionHighlighted: styles.formSchool__highlight,
              suggestionsList: styles.formSchool__suggestionList,
              suggestion: styles.formSchool__suggestion
            }}
            suggestions={suggestions}
            onSuggestionsFetchRequested={onSuggestionsFetchRequested}
            onSuggestionsClearRequested={onSuggestionsClearRequested}
            getSuggestionValue={getSuggestionValue}
            renderSuggestion={renderSuggestion}
            inputProps={inputProps}
            highlightFirstSuggestion={true}
          />
        </div>

        <div className={styles.formGroup}>
          <label className={styles.formLabel}>Thc mc/Gp </label>
          <textarea className={styles.formFeedbackInput} id="formFeedback" />
        </div>

        {isLoading ? (
          <>
            <p>H thng ang x l, vui lng ch trong t nht 15 giy {'<3'}</p>
            <p className={styles.formSubmitButton}>ang ng k...</p>
          </>
        ) : (
          <button type="button" onClick={handleSubmitButton} className={styles.formSubmitButton}>
            Hon thnh kho st
          </button>
        )}
      </form>
    </div>
  )
}
