import React, { useState, useEffect } from 'react'
import { Button, Form } from 'react-bootstrap'
import FileUploader from 'shared/components/form/FileUploader'
import InputField from 'shared/components/form/InputField'
import SelectField from 'shared/components/form/SelectField'
import photocopyApi from 'api/photocopyApi'
import { toastWrapper } from 'utils'
import RadioField from 'shared/components/form/RadioField'
import { useForm } from 'react-hook-form'
import styled from 'styled-components'
import { AiOutlineDelete } from 'react-icons/ai'
import OrderInfo from './OrderInfo'
import { useModal } from 'react-modal-hook'
import ConfirmModal from 'shared/components/modal/ConfirmModal'

function CreationForm() {
  const [deleteIndex, setDeleteIndex] = useState()
  const [appliedCoupon, setAppliedCoupon] = useState()
  const [showModal, hideModal] = useModal(() => {
    const data = {
      title: 'Xa tp',
      body: 'Bn c chc chn mun xa tp ny?'
    }
    return <ConfirmModal hideModal={hideModal} data={data} onConfirm={onDeleteConfirm} />
  })

  const [orderInfo, setOrderInfo] = useState(false)
  const photocopyInfo = JSON.parse(localStorage.getItem('photocopy-info') || '{}')
  const [fileIds, setFileIds] = useState([])
  const [fileNames, setFileNames] = useState([])
  const [fileUploading, setFileUploading] = useState(false)
  const [receiptUploading, setReceiptUploading] = useState(false)
  const [receiptId, setReceiptId] = useState([])
  const [receiptName, setReceiptName] = useState([])
  const [categories, setCategories] = useState([])
  const [offices, setOffices] = useState([])
  const deliveryOptions = [
    { label: 'Nhn ti ca hng', value: '0' },
    { label: 'Giao hng tn ni', value: '1' }
  ]
  const addressOptions = [
    { label: 'K tc x Khu A', value: 'KTX Khu A' },
    { label: 'K tc x Khu B', value: 'KTX Khu B' }
  ]
  const [isDelivered, setIsDelivered] = useState('0')
  const { handleSubmit, control, setValue, watch } = useForm({
    mode: 'onBlur',
    reValidateMode: 'onChange',
    defaultValues: {
      name: photocopyInfo?.name,
      tel: photocopyInfo?.tel,
      zalo: photocopyInfo?.zalo,
      instruction: photocopyInfo?.instruction
    },
    resolver: undefined,
    context: undefined,
    criteriaMode: 'firstError',
    shouldFocusError: true,
    shouldUnregister: true,
    shouldUseNativeValidation: false,
    delayError: undefined
  })

  const handleDeliveryChange = value => {
    setIsDelivered(value)
  }

  const { coupon } = watch()
  const applyCoupon = () => {
    photocopyApi
      .applyCoupon(coupon?.toUpperCase())
      .then(res => {
        toastWrapper('p dng m gim gi thnh cng', 'success')
        setAppliedCoupon(res?.data)
      })
      .catch(e => {
        toastWrapper(e?.response?.data?.data?.message || 'M gim gi khng hp l', 'error')
      })
  }

  const onSubmit = data => {
    if (fileUploading || receiptUploading) {
      return toastWrapper('Vui lng ch ti tp ln hon tt!', 'info')
    }

    if (data?.category) {
      data.category = data.category.value
    }

    if (data?.office) {
      data.office = data.office.value
    }

    if (data?.address) {
      data.address = data.address.value
    }

    if (appliedCoupon) {
      data.coupon = appliedCoupon?._id
    } else {
      delete data.coupon
    }

    localStorage.setItem('photocopy-info', JSON.stringify(data))
    const order = {
      ...data,
      ...(fileIds.length > 0 ? { document: fileIds } : {}),
      ...(receiptId.length > 0 ? { receipt: receiptId } : {}),
      isDelivered: isDelivered === '1'
    }

    if (!order?.document) {
      return toastWrapper('Vui lng ti ln tp hoc nhp lin kt n ti liu!', 'error')
    }

    photocopyApi
      .addOrder(order)
      .then(res => {
        setFileIds([])
        setReceiptId([])
        setFileNames([])
        setReceiptName('')
        setValue('document', '', { shouldValidate: true })
        toastWrapper(res?.message || 'To n hng thnh cng!', 'success')
        setTimeout(() => {
          setOrderInfo(res?.data)
        }, 1000)
      })
      .catch(error => {
        toastWrapper(
          error?.response?.data?.data?.message || error?.response?.data?.message || 'To n hng tht bi!',
          'error'
        )
      })
  }

  useEffect(() => {
    photocopyApi
      .getCategories()
      .then(data => {
        setCategories(data?.data.map(c => ({ label: c?.name, value: c?._id })))
      })
      .catch(error => {
        toastWrapper(error?.response?.data?.message, 'error')
      })

    photocopyApi
      .getOffices()
      .then(data => {
        setOffices(data?.data.map(c => ({ label: c?.name, value: c?._id })))
      })
      .catch(error => {
        toastWrapper(error?.response?.data?.message, 'error')
      })
  }, [])

  const handleFileUpload = value => {
    if (fileIds.length > 10) return toastWrapper('Ch c th ti ln ti a 10 tp', 'error')
    setFileIds(prev => [...prev, `https://drive.google.com/file/d/${value}`])
  }
  const handleFileNames = value => setFileNames(prev => [...prev, value])

  const onDeleteConfirm = () => {
    if (typeof deleteIndex === 'number') {
      setFileIds(prev => {
        const temp = [...prev]
        temp.splice(deleteIndex, 1)
        return temp
      })
      setFileNames(prev => {
        const temp = [...prev]
        temp.splice(deleteIndex, 1)
        return temp
      })
      setDeleteIndex(null)
    }
  }

  if (orderInfo) {
    return <OrderInfo {...orderInfo} />
  }

  return (
    <Styles>
      <Form onSubmit={handleSubmit(onSubmit)}>
        <div className="files-stack d-flex flex-column justify-content-center align-items-start">
          {fileNames?.map((name, index) => {
            return (
              <div key={`${name}_${index}`} className="d-flex align-items-center justify-content-between w-100">
                <span style={{ overflowWrap: 'anywhere' }}>
                  {index + 1}. {name}
                </span>
                {index !== deleteIndex && (
                  <button
                    type="button"
                    className="btn ms-2"
                    onClick={() => {
                      setDeleteIndex(index)
                    }}
                  >
                    <AiOutlineDelete color="red" />
                  </button>
                )}
                {typeof deleteIndex === 'number' && deleteIndex === index && (
                  <button type="button" className="btn btn-outline-danger text-danger" onClick={onDeleteConfirm}>
                    Nhn  xa
                  </button>
                )}
              </div>
            )
          })}
        </div>
        <FileUploader
          setFileId={handleFileUpload}
          setFileName={handleFileNames}
          uploading={fileUploading}
          setUploading={setFileUploading}
          url={'/photocopy/upload/file'}
          name="document"
          text={fileIds.length > 0 ? 'Thm ti liu khc' : 'Thm ti liu'}
        />
        {fileIds.length === 0 && (
          <InputField
            label={'Hoc nhp lin kt n ti liu'}
            control={control}
            name="document"
            rules={{ required: false }}
          />
        )}
        <InputField
          label={'Hng dn in'}
          // placeholder='Nhp hng dn in cho nhn vin'
          as="textarea"
          rows={3}
          control={control}
          name="instruction"
        />
        <SelectField options={categories} label="Th loi" control={control} name="category" />
        <InputField label={'Tn ca bn'} control={control} name="name" />
        <InputField label={'in thoi lin h'} control={control} name="tel" />
        <InputField label={'Zalo'} control={control} name="zalo" />
        <RadioField
          label="Hnh thc giao hng"
          options={deliveryOptions}
          name="deliveryType"
          onChange={handleDeliveryChange}
          checkValue={isDelivered}
        />
        {isDelivered === '0' && (
          <SelectField options={offices} label="Chn chi nhnh" control={control} name="office" />
        )}
        {isDelivered === '1' && (
          <SelectField options={addressOptions} label="Chn khu vc" control={control} name="address" />
        )}
        {/* <FileUploader
          setFileId={(value) =>
            setReceiptId((prev) => `https://drive.google.com/file/d/${value}`)
          }
          label={"Ti ln ha n t cc (nu c)"}
          uploading={receiptUploading}
          setUploading={setReceiptUploading}
          setFileName={setReceiptName}
          url={'/photocopy/upload/receipt'}
          name="receipt"
        /> */}
        <p className="w-100 text-center form-text">{receiptName}</p>
        <Form.Group className="d-flex align-items-end">
          <InputField
            label={'M gim gi (nu c)'}
            control={control}
            name="coupon"
            rules={{ required: false }}
            disabled={!!appliedCoupon}
          />
          <Button className="coupon-btn ms-3 btn btn-primary" onClick={applyCoupon} disabled={!!appliedCoupon}>
            {appliedCoupon ? ' p dng' : 'p dng'}
          </Button>
        </Form.Group>
        <Form.Text>{appliedCoupon?.title}</Form.Text>
        <InputField
          label={'Ghi ch/Gp '}
          placeholder="Nhp ghi ch cho n hng"
          as="textarea"
          rows={3}
          control={control}
          name="note"
          rules={{ required: false }}
        />
        <Button className="custom-btn-primary submit-btn" type="submit">
          Gi ti liu ngay
        </Button>
      </Form>
    </Styles>
  )
}

export default CreationForm

const Styles = styled.div`
  .submit-btn {
    width: 100%;
    margin: 1rem 0 5rem;
  }

  .coupon-btn {
    min-width: fit-content;
  }
`
